.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

header {
  width: 100%;
  background-color: #F5F6F3;
  color: #632768;
  min-height: fit-content;
  text-align: center;
  display: flex;
  margin: 0;
  height: 100%;
  font-size: 5.5vw;
  padding: 0%;
}

div.header {
  height: 20vh;
  width: 100%;
}

div.title {
  width: 70vw;
  vertical-align: middle;
}

div.logo {
  width: 15vw;
}

text {
  align-items: center;
}

img.logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
  justify-content: left;
}